<template><section><!--
 * @Author: Fone丶峰
 * @Date: 2020-01-02 15:44:14
 * @LastEditors: Fone丶峰
 * @LastEditTime: 2020-05-12 15:40:08
 * @Description: msg
 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>Checkbox 多选</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Checkbox, CheckboxGroup } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Checkbox)
Vue.component(CheckboxGroup)

</code></pre>
<h4>基础用法</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span>&gt;</span>选项<span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>

</code></pre>
<h4>type类型</h4>
<p><code v-pre>type</code>类型提供<code v-pre>default</code>、<code v-pre>errer</code>、<code v-pre>success</code>、<code v-pre>warning</code>四种可选，默认<code v-pre>default</code></p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Checkbox-Group</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"default"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"default"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"errer"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"errer"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"success"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"warning"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox-Group</span>&gt;</span>

</code></pre>
<h4>配合CheckboxGroup使用</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Checkbox-Group</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项一"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项二"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项三"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox-Group</span>&gt;</span>

</code></pre>
<h4>v-model绑定</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox-Group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"model"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项一"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项二"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项三"</span> /&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox-Group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">model</span>: [<span class="hljs-string">"选项二"</span>]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>禁用</h4>
<p>可通过<code v-pre>disabled</code>属性禁用<code v-pre>Checkbox</code>组件</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Checkbox-Group</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"['选项二']"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项一"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项二"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项三"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox-Group</span>&gt;</span>

</code></pre>
<h4>CheckboxGroup的type属性</h4>
<p>也可通过<code v-pre>CheckboxGroup</code>组件的<code v-pre>type</code>控制所有<code v-pre>Checkbox</code>组件的<code v-pre>type</code>，<code v-pre>disabled</code>属性和<code v-pre>icon</code>属性也可这样操作</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Checkbox-Group</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"['选项二']"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项一"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项二"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项三"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox-Group</span>&gt;</span>

</code></pre>
<h4>change事件</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox-Group</span> <span class="hljs-attr">@on-change</span>=<span class="hljs-string">"change"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项一"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项二"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"选项三"</span> /&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">Checkbox-Group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    change(val) {
      <span class="hljs-keyword">this</span>.$Toast.info(<span class="hljs-string">`选中：<span class="hljs-subst">${val}</span>`</span>);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Checkbox props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>绑定值</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>type</td>
<td>不同的类型对应不同的主题色彩</td>
<td>String</td>
<td><code v-pre>default</code> <code v-pre>errer</code> <code v-pre>success</code> <code v-pre>warning</code></td>
<td>default</td>
</tr>
<tr>
<td>label</td>
<td>选中状态的值</td>
<td>String/Number/Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>icon</td>
<td>自定义Checkbox选中的icon</td>
<td>String</td>
<td>无</td>
<td>iconselected</td>
</tr>
</tbody>
</table>
<h3>API</h3>
<h5>CheckboxGroup props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>绑定值</td>
<td>Array</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>type</td>
<td>设置该属性可控制Checkbox的type属性</td>
<td>String</td>
<td><code v-pre>default</code> <code v-pre>errer</code> <code v-pre>success</code> <code v-pre>warning</code></td>
<td>无</td>
</tr>
<tr>
<td>inline</td>
<td>是否开启行内块</td>
<td>Boolean</td>
<td>无</td>
<td>true</td>
</tr>
<tr>
<td>disabled</td>
<td>设置该属性可控制Checkbox的disabled属性</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>icon</td>
<td>设置该属性可控制Checkbox的icon属性</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>Checkbox solt</h5>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>自定义主体内容</td>
</tr>
</tbody>
</table>
<h5>Checkbox Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>Checkbox变化时触发，嵌套CheckboxGroup组件使用时无效</td>
<td>选中的label值</td>
</tr>
</tbody>
</table>
<h5>CheckboxGroup Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>绑定值变化时触发</td>
<td>选中的label值数组</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      model: ["选项二"]
    };
  }
};
</script>


